<template>
  <h2>我是About的内容</h2>
</template>

<script>
export default {
  name: 'About',
  //通过路由规则，进入该组件时被调用
  beforeRouteEnter(to, from, next) {
    if (to.meta.isAuth) {//判断是否需要鉴权
      if (localStorage.getItem('school') === 'atguigu') {
        // document.title = to.meta.title || '硅谷系统'
        next()
      } else {
        alert('学校名不对，无权限！！')
      }
    } else {
      // document.title = to.meta.title || '硅谷系统'
      next()
    }
  },
  //通过路由规则，离开该组件时被调用
  beforeRouteLeave(to, from, next) {
    next()
  },
}
</script>

